*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Courier New', Courier, monospace;
  line-height: 1.4;
}
a{
  color: #333;
  text-decoration: none;
}
.container{
  padding: 0 1rem;
}
.btn{
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7x 20px;
  cursor: pointer;
}
.btn:hover {
  background: #666;
}